import React, { useState } from 'react';

// Material
import { List, ListItem, ListItemSecondaryAction, ListItemText,
    IconButton, Paper, Grid, Tooltip, Button,
    DialogActions, DialogTitle, Dialog } from '@material-ui/core';
import { ShoppingCart, Close } from '@material-ui/icons';

interface List {
    price: number;
    chest: number;
    k: string;
    name: string;
}

interface Props {
    list: List[] | undefined;
    updateOwned: () => void;
}

const Owned: React.FC<Props> = ({ list, updateOwned }) => {
    const [open, setOpen] = useState<boolean>(false);
    const [selected, setSelected] = useState<string | undefined>();

    const openSell = (veh: string) => {
        setSelected(veh);
        setOpen(true);
    }
    const closeSell = () => {
        setSelected(undefined);
        setOpen(false);
    }
    const doSell = () => {
        if (selected === undefined) return;

        fetch("http://bdonate/RequestSell", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({ name: selected })
        })
        .then(res => res.json())
        .then(cb => {
            setOpen(false);
            setSelected(undefined);
            updateOwned();
        })
        .catch(err => console.log(err));
    }

    return (
        <List>
            <Grid container spacing={2}>
                {list?.map(veh => (
                    <Grid item xs={6}>
                        <Paper>
                            <ListItem>
                                <ListItemText
                                    primary={veh.name}
                                    secondary={`${Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD'}).format(veh.price)} - ${veh.chest}KG`}
                                />
                                <ListItemSecondaryAction style={{right: "4px"}}>
                                    <Tooltip title="Vender" arrow>
                                        <IconButton onClick={() => openSell(veh.k)}>
                                            <ShoppingCart />
                                        </IconButton>
                                    </Tooltip>
                                </ListItemSecondaryAction>
                            </ListItem>
                        </Paper>
                    </Grid> 
                ))}
            </Grid>
            <Dialog
                open={open}
                onClose={closeSell}
            >
                <DialogTitle>Tem certeza que deseja vender o seu {selected}?</DialogTitle>
                <DialogActions style={{ justifyContent: 'space-between', padding: '16px'}}>
                    <div>
                        <Button
                            color="primary"
                            startIcon={<ShoppingCart />}
                            onClick={doSell}
                        >
                            Vender
                        </Button>
                    </div>
                    <div>
                        <Button
                            color="secondary"
                            startIcon={<Close />}
                            onClick={closeSell}
                        >
                            Cancelar
                        </Button>
                    </div>
                </DialogActions>
            </Dialog>
        </List>
    )
}

export default Owned;