import React, { useState, useEffect } from 'react';
import './styles.scss';

// Pages
import Cars from '../Cars';
import Bikes from '../Bikes';
import Owned from '../Owned';

// Material
import { BottomNavigation,
    BottomNavigationAction, Paper, IconButton } from '@material-ui/core';
    import { DriveEta, Motorcycle, Bookmarks, Search } from '@material-ui/icons';

interface List {
    price: number;
    chest: number;
    k: string;
    name: string;
}

const Dealership: React.FC = () => {
    const [page, setPage] = useState<number>(0);
    // List
    const [cars, setCars] = useState<List[] | undefined>();
    // Search
    const [search, setSearch] = useState<string>("");

    const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSearch(e.target.value);
        if (e.target.value !== "") {
            const teste = cars?.filter((element) => {
                const name = element.name.toLowerCase();
                return name.includes(e.target.value.toLowerCase());
            });

            if (teste === undefined) return;
            const namelist = teste.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
            setCars(namelist);
        } else {
            if (page === 0) {
                fetch("http://bdonate/GetCars", {
                    headers: {
                        "Content-Type": "application/json"
                    },
                    method: "POST",
                    body: JSON.stringify({})
                })
                .then(res => res.json())
                .then(cb => {
                    const namelist = cb.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
                    setCars(namelist);
                    setPage(0);    
                })
            } else if (page === 1) {
                fetch("http://bdonate/GetBikes", {
                    headers: {
                        "Content-Type": "application/json"
                    },
                    method: "POST",
                    body: JSON.stringify({})
                })
                .then(res => res.json())
                .then(cb => {
                    const namelist = cb.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
                    setCars(namelist);
                    setPage(1);    
                })
            }
        }     
    }

    const updateOwned = () => {
        fetch("http://bdonate/GetOwned", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({})
        })
        .then(res => res.json())
        .then(cb => {
            const namelist = cb.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
            setCars(namelist);
            setPage(2);
        })
        .catch(err => console.log(err));
    }

    const handleSpawn = (veh: string, slot: number) => {
        fetch("http://bdonate/HandleSpawn", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({
                vehicle: veh,
                slot: slot
            })
        })
        .catch(err => console.log(err));
    }
    const handleEvent = (event:any) => {
        if (event === undefined) return;
        const message = event.data;

        switch(message.action) {
            case "show":
                fetch("http://bdonate/GetCars", {
                    headers: {
                        "Content-Type": "application/json"
                    },
                    method: "POST",
                    body: JSON.stringify({})
                })
                .then(res => res.json())
                .then(cb => {
                    const namelist = cb.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
                    setCars(namelist);
                    setPage(0);
                    (document.getElementById("root") as HTMLDivElement).style.display = "block";
                })
                .catch(err => console.log(err));
                break;
        }
    }

    useEffect(() => {
        window.addEventListener("message", handleEvent);
        window.onkeyup = (data:any) => {
            if (data.which === 27) {
                (document.getElementById("root") as HTMLDivElement).style.display = "none";
                fetch("http://bdonate/CloseApp", {
                    headers: {
                        "Content-Type": "application/json"
                    },
                    method: "POST",
                    body: JSON.stringify({})
                })
                .catch(err => console.log(err));
            }
        };
        return () => {
            window.removeEventListener("message", handleEvent);
        }
    }, [handleEvent]);

    return (
        <div className="dealerContainer">
            {page === 0 || page === 1 ? (
                <Paper className="searchBar">
                    <IconButton>
                        <Search />
                    </IconButton>
                    <input
                        type="text"
                        className="searchField"
                        placeholder="Buscar veÃ­culo..."
                        value={search}
                        onChange={handleSearch}
                    />
                </Paper>
            ) : ("")}
            <div className="vehiclesContainer">
                {
                    page === 0 ?
                    <Cars list={cars} handleSpawn={handleSpawn} />:
                    page === 1 ?
                    <Bikes list={cars} handleSpawn={handleSpawn} />:
                    <Owned list={cars} updateOwned={updateOwned} />
                }
            </div>
            <BottomNavigation
                value={page}
                className="dealerNav"
                onChange={(e, newVal) => {
                    if (newVal === 0) {
                        fetch("http://bdonate/GetCars", {
                            headers: {
                                "Content-Type": "application/json"
                            },
                            method: "POST",
                            body: JSON.stringify({})
                        })
                        .then(res => res.json())
                        .then(cb => {
                            const namelist = cb.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
                            setCars(namelist);
                            setPage(newVal);
                        })
                        .catch(err => console.log(err));
                    } else if (newVal === 1) {
                        fetch("http://bdonate/GetBikes", {
                            headers: {
                                "Content-Type": "application/json"
                            },
                            method: "POST",
                            body: JSON.stringify({})
                        })
                        .then(res => res.json())
                        .then(cb => {
                            const namelist = cb.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
                            setCars(namelist);
                            setPage(newVal);
                        })
                        .catch(err => console.log(err));
                    } else if (newVal === 2) {
                        fetch("http://bdonate/GetOwned", {
                            headers: {
                                "Content-Type": "application/json"
                            },
                            method: "POST",
                            body: JSON.stringify({})
                        })
                        .then(res => res.json())
                        .then(cb => {
                            const namelist = cb.sort((a:any,b:any) => (a.name > b.name) ? 1: -1);
                            setCars(namelist);
                            setPage(newVal);
                        })
                        .catch(err => console.log(err));
                    }
                }}
            >
                <BottomNavigationAction icon={<DriveEta />} />
                <BottomNavigationAction icon={<Motorcycle />} />
                <BottomNavigationAction icon={<Bookmarks />} />
            </BottomNavigation>
        </div>
    );
}

export default Dealership;