import React, { useState } from 'react';

// Material
import { List, ListItem, ListItemSecondaryAction, ListItemText,
    IconButton, Paper, Grid, Tooltip, Menu, MenuItem,
    Button, DialogActions, DialogTitle, Dialog} from '@material-ui/core';
import { AttachMoney, CameraAlt, Close, AccountBalanceWallet } from '@material-ui/icons';

interface List {
    price: number;
    chest: number;
    k: string;
    name: string;
}

interface Props {
    list: List[] | undefined;
    handleSpawn: (veh: string, slot: number) => void;
}

const slots = [1,2,3,4];

const Bikes: React.FC<Props> = ({ list, handleSpawn }) => {
    const [open, setOpen] = useState<boolean>(false);
    const [selected, setSelected] = useState<string | undefined>();
    
    const openBuy = (veh: string) => {
        setSelected(veh);
        setOpen(true);
    }
    const closeBuy = () => {
        setSelected(undefined);
        setOpen(false);
    }
    const doBuy = () => {
        if (selected === undefined) return;

        fetch("http://bdonate/RequestBuy", {
            headers: {
                "Content-Type": "application/json"
            },
            method: "POST",
            body: JSON.stringify({
                name: selected,
                form: "vista"
            })
        })
        .then(res => res.json())
        .then(cb => {
            setOpen(false);
            setSelected(undefined);
        })
        .catch(err => console.log(err));
    }
    const handleSlot = (slot: number) => {
        if (selected === undefined) return;

        handleSpawn(selected, slot);
        setAnchor(null);
        setSelected(undefined);
    }

    // Anchor Menu
    const [anchor, setAnchor] = useState<null | HTMLElement>(null);
    const handleClick = (e: React.MouseEvent<HTMLButtonElement>, veh: string) => {
        setAnchor(e.currentTarget);
        setSelected(veh);
    }
    const handleClose = () => {
        setAnchor(null);
        setSelected(undefined);
    }

    return (
        <List>
            <Grid container spacing={2}>
                {list?.map(veh => (
                    <Grid item xs={6}>
                        <Paper>
                            <ListItem>
                                <ListItemText
                                    primary={veh.name}
                                    secondary={`${Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD'}).format(veh.price)} - ${veh.chest}KG`}
                                />
                                <ListItemSecondaryAction style={{right: "4px"}}>
                                    <Tooltip title="Visualizar" arrow>
                                        <IconButton onClick={(e) => handleClick(e, veh.k)}>
                                            <CameraAlt />
                                        </IconButton>
                                    </Tooltip>
                                    <Tooltip title="Comprar" arrow>
                                        <IconButton onClick={() => openBuy(veh.k)}>
                                            <AttachMoney />
                                        </IconButton>
                                    </Tooltip>
                                </ListItemSecondaryAction>
                            </ListItem>
                        </Paper>
                    </Grid> 
                ))}
            </Grid>
            <Menu
                id="simple-menu"
                anchorEl={anchor}
                keepMounted
                open={Boolean(anchor)}
                onClose={handleClose}
            >
                {slots.map(slot => (
                    <MenuItem onClick={() => handleSlot(slot)}>{slot}</MenuItem>
                ))}
            </Menu>
            <Dialog
                open={open}
                onClose={closeBuy}
            >
                <DialogTitle>Tem certeza que deseja comprar uma {selected}?</DialogTitle>
                <DialogActions style={{ justifyContent: 'space-between', padding: '16px'}}>
                    <Button
                        color="primary"
                        startIcon={<AccountBalanceWallet />}
                        onClick={doBuy}
                    >
                        Comprar
                    </Button>
                    <Button
                        color="secondary"
                        startIcon={<Close />}
                        onClick={closeBuy}
                    >
                        Cancelar
                    </Button>
                </DialogActions>
            </Dialog>
        </List>
    )
}

export default Bikes;